export function getOptimalBleedPercent(deviceType: string): number {
  switch (deviceType) {
    case 'laptop':
    case 'desktop':
      // 
      // 12%
      return 12; // 12%
    case 'smartphone':
      // 5%
      return 5;
    case 'tablet':
      // 
      return 8; // 
    default:
      return 5;
  }
}
