/**
 * 
 * 
 */

export interface Point {
  x: number;
  y: number;
}

export type ShapePattern = 'rectangle' | 'parallelogram' | 'trapezoid' | 'irregular';

/**
 * 2
 */
function distance(p1: Point, p2: Point): number {
  const dx = p2.x - p1.x;
  const dy = p2.y - p1.y;
  return Math.sqrt(dx * dx + dy * dy);
}

/**
 * 3
 * @param p1 
 * @param vertex 
 * @param p2 
 * @returns 
 */
function calculateAngle(p1: Point, vertex: Point, p2: Point): number {
  // 
  const v1 = { x: p1.x - vertex.x, y: p1.y - vertex.y };
  const v2 = { x: p2.x - vertex.x, y: p2.y - vertex.y };

  // 
  const mag1 = Math.sqrt(v1.x * v1.x + v1.y * v1.y);
  const mag2 = Math.sqrt(v2.x * v2.x + v2.y * v2.y);

  if (mag1 === 0 || mag2 === 0) return 0;

  // 
  const dot = v1.x * v2.x + v1.y * v2.y;

  // cos
  const cosTheta = dot / (mag1 * mag2);

  // 
  const angleRad = Math.acos(Math.max(-1, Math.min(1, cosTheta)));
  const angleDeg = (angleRad * 180) / Math.PI;

  return angleDeg;
}

/**
 * 4
 * @param corners 4 [, , , ]
 * @returns 
 */
export function detectShapePattern(
  corners: [Point, Point, Point, Point]
): ShapePattern {
  // 4
  const side1 = distance(corners[0], corners[1]); // 
  const side2 = distance(corners[1], corners[2]); // 
  const side3 = distance(corners[2], corners[3]); // 
  const side4 = distance(corners[3], corners[0]); // 

  // 4
  const angle1 = calculateAngle(corners[3], corners[0], corners[1]); // 
  const angle2 = calculateAngle(corners[0], corners[1], corners[2]); // 
  const angle3 = calculateAngle(corners[1], corners[2], corners[3]); // 
  const angle4 = calculateAngle(corners[2], corners[3], corners[0]); // 

  // 
  const oppositeSide1Diff = Math.abs(side1 - side3) / Math.max(side1, side3);
  const oppositeSide2Diff = Math.abs(side2 - side4) / Math.max(side2, side4);

  // 
  const allAnglesNearRight = [angle1, angle2, angle3, angle4]
    .every(a => a >= 85 && a <= 95);

  // 1. 
  if (
    allAnglesNearRight &&
    oppositeSide1Diff < 0.05 &&
    oppositeSide2Diff < 0.05
  ) {
    return 'rectangle';
  }

  // 2. 
  const parallelCheck =
    oppositeSide1Diff < 0.1 &&
    oppositeSide2Diff < 0.1;

  const hasNonRightAngles = [angle1, angle2, angle3, angle4]
    .some(a => (a >= 75 && a < 85) || (a > 95 && a <= 105));

  if (parallelCheck && hasNonRightAngles) {
    return 'parallelogram';
  }

  // 3. 
  if (oppositeSide1Diff > 0.1 || oppositeSide2Diff > 0.1) {
    return 'trapezoid';
  }

  return 'irregular';
}

/**
 * 
 */
export interface ShapeAnalysis {
  pattern: ShapePattern;
  sides: [number, number, number, number]; // 4
  angles: [number, number, number, number]; // 4
  oppositeSideDiffs: [number, number]; // 
  description: string; // 
}

/**
 * 
 */
export function analyzeShape(
  corners: [Point, Point, Point, Point]
): ShapeAnalysis {
  const pattern = detectShapePattern(corners);

  // 4
  const sides: [number, number, number, number] = [
    distance(corners[0], corners[1]), // 
    distance(corners[1], corners[2]), // 
    distance(corners[2], corners[3]), // 
    distance(corners[3], corners[0]), // 
  ];

  // 4
  const angles: [number, number, number, number] = [
    calculateAngle(corners[3], corners[0], corners[1]), // 
    calculateAngle(corners[0], corners[1], corners[2]), // 
    calculateAngle(corners[1], corners[2], corners[3]), // 
    calculateAngle(corners[2], corners[3], corners[0]), // 
  ];

  // 
  const oppositeSideDiffs: [number, number] = [
    Math.abs(sides[0] - sides[2]) / Math.max(sides[0], sides[2]),
    Math.abs(sides[1] - sides[3]) / Math.max(sides[1], sides[3]),
  ];

  // 
  let description = '';
  switch (pattern) {
    case 'rectangle':
      description = '- 4';
      break;
    case 'parallelogram':
      description = '3D- ';
      break;
    case 'trapezoid':
      description = '- 11';
      break;
    case 'irregular':
      description = ' - ';
      break;
  }

  return {
    pattern,
    sides,
    angles,
    oppositeSideDiffs,
    description,
  };
}

/**
 * 
 */
export function getShapeScoreModifiers(pattern: ShapePattern): {
  laptopModifier: number;
  smartphoneModifier: number;
  tabletModifier: number;
} {
  switch (pattern) {
    case 'rectangle':
      // - 
      return {
        laptopModifier: 1.0,
        smartphoneModifier: 1.0,
        tabletModifier: 1.0,
      };

    case 'parallelogram':
    case 'trapezoid':
      // 3D- /
      return {
        laptopModifier: 1.2,
        smartphoneModifier: 0.5, // 3D
        tabletModifier: 1.2,
      };

    case 'irregular':
      //  - 
      return {
        laptopModifier: 1.0,
        smartphoneModifier: 1.0,
        tabletModifier: 1.0,
      };
  }
}
